

import { useState } from "react";
import { Lock, User, KeyRound, ArrowRight } from "lucide-react";

interface LoginFormProps {
  onSubmit: (data: {
    usernameOrEmail: string;
    password: string;
    rememberMe: boolean;
  }) => void;
}

const LoginForm: React.FC<LoginFormProps> = ({ onSubmit }) => {
  const [usernameOrEmail, setUsernameOrEmail] = useState("");
  const [password, setPassword] = useState("");
  const [rememberMe, setRememberMe] = useState(false);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit({ usernameOrEmail, password, rememberMe });
  };

  return (
    <div className="flex justify-center items-center min-h-screen bg-gray-50 p-4">
      <div className="bg-white p-8 md:p-12 rounded-lg shadow-xl w-full max-w-sm">
        <div className="flex items-center mb-6">
          <div className="p-3 mr-4 rounded-lg bg-white border border-gray-200">
            <Lock className="w-6 h-6 text-red-600" strokeWidth={2} />
          </div>
          <div>
            <h2 className="text-3xl font-bold text-gray-800">Login Here</h2>
          </div>
        </div>

        <p className="text-sm text-gray-500 mb-8">
          Your personal data will be used to support your experience throughout
          this website, to manage access to your account.
        </p>

        <form onSubmit={handleSubmit} className="space-y-6">
          <div>
            <div className="flex items-center border-b border-gray-300 py-3 focus-within:border-red-600 transition-colors duration-200">
              <User className="w-5 h-5 text-gray-500 mr-3" strokeWidth={1.5} />
              <input
                type="text"
                placeholder="Username / email address"
                value={usernameOrEmail}
                onChange={(e) => setUsernameOrEmail(e.target.value)}
                className="w-full text-lg appearance-none bg-transparent border-none focus:outline-none placeholder-gray-500"
                required
              />
            </div>
          </div>

          <div>
            <div className="flex items-center border-b border-gray-300 py-3 focus-within:border-red-600 transition-colors duration-200">
              <KeyRound className="w-5 h-5 text-gray-500 mr-3" strokeWidth={1.5} />
              <input
                type="password"
                placeholder="Password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full text-lg appearance-none bg-transparent border-none focus:outline-none placeholder-gray-500"
                required
              />
            </div>
          </div>

          <div className="flex justify-between items-center pt-2">
            <label className="flex items-center cursor-pointer">
              <input
                type="checkbox"
                checked={rememberMe}
                onChange={(e) => setRememberMe(e.target.checked)}
                className="form-checkbox h-4 w-4 text-red-600 rounded border-gray-300 focus:ring-red-500"
              />
              <span className="ml-2 text-sm text-gray-700">Remember me</span>
            </label>
            <a
              href="#"
              className="text-sm text-gray-500 hover:text-red-600 font-medium"
            >
              Forget Password
            </a>
          </div>

          <button
            type="submit"
            className="w-full flex justify-center items-center bg-red-600 hover:bg-red-700 text-white font-semibold py-3 px-4 rounded-lg transition duration-200 mt-8"
          >
            <span className="text-lg">Login Now</span>
            <ArrowRight className="w-5 h-5 ml-2" strokeWidth={2} />
          </button>
        </form>
      </div>
    </div>
  );
};

export default LoginForm;
